.form {
    display: flex;
    justify-content: center;
    align-items: center;

    padding-top: 30px;
}

.lable {
    margin-right: 10px;
}

.input {
    margin-left: 5px;

    background-color: #00bfff;
    border: 1px solid Yellow;
    border-radius: 5px;
    font-family: 'Comfortaa';
}

.input:focus-visible {
    outline: 1px solid Yellow;
}

.button {
    padding: 10px 10px;

    border-radius: 10px;
    border: 1px solid Yellow;
    background-color: Yellow;
    font-family: 'Comfortaa';
}
.button:hover {
    cursor: pointer;
}

.title {
    margin: 0;
    margin-top: 10px;
    font-size: 16px;
    text-align: center;
}
